import { isBang } from "../../sdk";
import Op from "./base";
import type { IInletsMeta, IOutletsMeta, IArgsMeta, IPropsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";
import { extractFirst } from "@jspatcher/jspatcher/src/core/message";

export default class Binary extends Op<[any, any], [any], [any], Record<string, any>> {
    static description = "Binary Operation";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "First element"
    }, {
        isHot: false,
        type: "anything",
        description: "Second element"
    }];
    static outlets: IOutletsMeta = [{
        type: "anything",
        description: "Result"
    }];
    static args: IArgsMeta = [{
        type: "anything",
        optional: true,
        default: 0,
        description: "Initial second element"
    }];

    static props: IPropsMeta = {
        forceInt: {
            type: "boolean",
            default: false,
            description: "Convert inputs and outputs to integers"
        }
    };
    prepareInput(input: any) {
        const forceInt = this.getProp("forceInt");
        if (input instanceof Array && input.length == 1) {
            const first = +extractFirst(input);
            return forceInt ? Math.floor(first) : first;
        }
        return forceInt ? Math.floor(+input) : +input;
    }
    handleUpdateArgs(args: any[]) {
        this._.arg = undefined;
        this._.result = undefined;
        if (!args || args.length === 0) return;
        this._.arg = this.prepareInput(args[0]);
    }
    execute: (a: any, b: any) => any;
    _ = { arg: this.args[0], result: undefined as any };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 2;
            this.outlets = 1;
            this.handleUpdateArgs(this.args);
        });
        this.on("updateArgs", (args) => {
            this.handleUpdateArgs(args);
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {
                        const result = this.execute(this.prepareInput(data), this._.arg);
                        this._.result = this.prepareInput(result);
                    } catch (e) {
                        this.error(e);
                        return;
                    }
                }
                this.outlet(0, this._.result);
            } else if (inlet === 1) {
                this._.arg = this.prepareInput(data);
            }
        });
    }
}
