import _Binary from "./objects/block/binary";
import BinaryAudio from "./objects/dsp/binary";
import _Unary from "./objects/block/unary";
import { BaseObject, generateDefaultObject } from "./sdk";
// import "./objects/ui/ui.scss";
import { generateObject } from "../../../common/web/jsDspObject";
import Add from "./objects/dsp/add";
import Mul from "./objects/dsp/mul";
import Sub from "./objects/dsp/sub";
import Div from "./objects/dsp/div";
import ReverseDiv from "./objects/dsp/rev_div";
import ReverseSub from "./objects/dsp/rev_sub";
import Hip from "./objects/dsp/hip";
import Lop from "./objects/dsp/lop";
import Bp from "./objects/dsp/bp";
import Exp from "./objects/dsp/exp";
import Abs from "./objects/dsp/abs";
import Log from "./objects/dsp/log";
import Sqrt from "./objects/dsp/sqrt";


// Generated objects
const Binary = generateDefaultObject(_Binary as typeof BaseObject);
const Unary = generateDefaultObject(_Unary as typeof BaseObject);

const BinaryObjects: Record<string, typeof Binary> = {};

const binary_functions: Record<string, { f: (...args: any[]) => any, n: string, d: string }> = {
    "+": { f: (a: any, b: any) => a + b, n: "Add", d: "math/docs/add.html" },
    "-": { f: (a: any, b: any) => a - b, n: "Sub", d: "math/docs/sub.html" },
    "!-": { f: (a: any, b: any) => b - a, n: "Reverse Sub", d: "" },
    "*": { f: (a: any, b: any) => a * b, n: "Mul", d: "math/docs/mul.html" },
    "/": { f: (a: any, b: any) => a / b, n: "Div", d: "math/docs/div.html" },
    "!/": { f: (a: any, b: any) => b / a, n: "Reverse Div", d: "" },
    "==": { f: (a: any, b: any) => a == b, n: "Equal", d: "math/docs/equal.html" },
    "!=": { f: (a: any, b: any) => a != b, n: "Not equal", d: "math/docs/notequal.html" },
    ">": { f: (a: any, b: any) => a > b, n: "Greater", d: "math/docs/greaterthan.html" },
    ">=": { f: (a: any, b: any) => a >= b, n: "Greater or equal", d: "math/docs/greaterthanorequal.html" },
    "<": { f: (a: any, b: any) => a < b, n: "Less", d: "math/docs/lessthan.html" },
    "<=": { f: (a: any, b: any) => a <= b, n: "Less or equal", d: "math/docs/lessthanorequal.html" },
    "&&": { f: (a: any, b: any) => Number((a > 0) && (b > 0)), n: "Boolean AND", d: "math/docs/andlogical.html" },
    "||": { f: (a: any, b: any) => Number((a > 0) || (b > 0)), n: "Boolean OR", d: "math/docs/orlogical.html" },
    "max": { f: (a: any, b: any) => a > b ? a : b, n: "Max", d: "math/docs/max.html" },
    "min": { f: (a: any, b: any) => a < b ? a : b, n: "Min", d: "math/docs/min.html" },
    "pow": { f: (a: any, b: any) => a ** b, n: "Power", d: "" },
    "%": { f: (a: any, b: any) => a % b, n: "Mod", d: "math/docs/mod.html" },
    "!%": { f: (a: any, b: any) => b % a, n: "Reverse Mod", d: "" },
    "log": {
        f: (a: any, b: any) => {
            if (a <= 0.) {
                return -1000.;
            }
            else if ((b == 1) || (b <= 0.)) {
                return Math.log(a);
            }
            else {
                return Math.log(a) / Math.log(b);
            }
        }, n: "Log", d: "math/docs/log.html"
    },
    "atan2": { f: (a: any, b: any) => Math.atan2(a, b), n: "Atan2", d: "math/docs/atan2.html" },
}

for (const key in binary_functions) {
    BinaryObjects[key] = class extends Binary {
        static description: string = binary_functions[key].n;
        static get _name() { return binary_functions[key].n; }
        static docs: string = binary_functions[key].d;
        execute = binary_functions[key].f;
    }
}


const UnaryObjects: Record<string, any> = {
    "exp~": generateObject(Exp, "Exp"),
    "abs~": generateObject(Abs, "Abs"),
    "sqrt~": generateObject(Sqrt, "Sqrt"),
}

const unary_functions: Record<string, { f: (...args: any[]) => any, n: string, d: string }> = {
    "!": { f: (a: any) => !a, n: "Not", d: "" },
    "~": { f: (a: any) => ~a, n: "Invert", d: "" },
    "exp": { f: (a: any) => Math.exp(a), n: "Exponential", d: "math/docs/exp.html" },
    "abs": { f: (a: any) => Math.abs(a), n: "Absolute Value", d: "math/docs/abs.html" },
    "sqrt": { f: (a: any) => a > 0. ? Math.sqrt(a) : 0., n: "Square Root", d: "math/docs/sqrt.html" },
    "cos": { f: (a: any) => Math.cos(a), n: "Cosine", d: "math/docs/cos.html" },
    "sin": { f: (a: any) => Math.sin(a), n: "Sine", d: "math/docs/sin.html" },
    "tan": { f: (a: any) => Math.tan(a), n: "Tangent", d: "math/docs/tan.html" },
    "atan": { f: (a: any) => Math.atan(a), n: "Arc Tangent", d: "math/docs/atan.html" },
}

for (const key in unary_functions) {
    UnaryObjects[key] = class extends Unary {
        static description: string = unary_functions[key].n;
        static get _name() { return unary_functions[key].n; }
        static docs: string = unary_functions[key].d;
        execute = unary_functions[key].f;
    }
}

