const BinaryAudioObjects: Record<string, any> = {
    "+~": generateObject(Add, "Add"),
    "-~": generateObject(Sub, "Sub"),
    "*~": generateObject(Mul, "Mul"),
    "/~": generateObject(Div, "Div"),
    "!/~": generateObject(ReverseDiv, "Reverse Div"),
    "!-~": generateObject(ReverseSub, "Reverse Sub"),
    "log~": generateObject(Log, "Log"),
}

const FilterAudioObjects: Record<string, any> = {
    "hip~": generateObject(Hip, "Hip"),
    "lop~": generateObject(Lop, "Lop"),
    "bp~": generateObject(Bp, "Bp"),
}


export default async () => ({
    ...BinaryObjects,
    ...UnaryObjects,
    ...BinaryAudioObjects,
    ...FilterAudioObjects,
});
