import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Lop extends JsDspProcessor {
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "audio signal",
            varLength: true
        },
        {
            isHot: true,
            type: "signal",
            description: "rolloff frequency",
            varLength: true
        }
    ]
    static outlets: IOutletsMeta = [
        {
            type: "signal",
            description: "output",
            varLength: true
        }
    ]
    static args: IArgsMeta = [
        {
            type: "number",
            optional: true,
            description: "rolloff frequency",
            default: 1000
        }
    ]
    static argsOffset: number = 1;
    static docs: string = "math/docs/lop.html";


    sample_rate_: number;
    conversion_: number;
    last_: number;
    hz_: number;
    coef_: number;

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        let inputStream = inputs[0][0];
        let frequency = inputs[0][1];
        let outputStream = outputs[0][0];

        this.conversion_ = (2. * Math.PI) / this.sample_rate_;

        let coef = 0.;
        let feedback = 0.;
        let last = this.last_;

        for (let i = 0; i < inputStream.length; i++) {

            last = this.last_;

            if (frequency[i] != this.hz_) {

                this.hz_ = frequency[i];
                coef = frequency[i] * this.conversion_;

                if (coef > 1) {
                    coef = 1.;
                }
                else if (coef < 0) {
                    coef = 0.;
                }

                this.coef_ = coef;

            }
            else {
                coef = this.coef_;
            }

            feedback = 1. - coef;

            last = coef * inputStream[i] + feedback * last;
            outputStream[i] = last;

            this.last_ = last;

        }

        return true;
    }

    init(sampleRate: number) {

        this.sample_rate_ = sampleRate;
        this.conversion_ = 0.;
        this.last_ = 0.;
        this.hz_ = 0.;
        this.coef_ = 0.;
    }
}
