import { extractFirst } from "@jspatcher/jspatcher/src/core/message";
import { isBang } from "../../sdk";
import Op from "./base";
import type { IInletsMeta, IOutletsMeta, IArgsMeta, IPropsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Unary extends Op<[any], [any], [any], Record<string, any>> {
    static description = "Unary Operation";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "First element"
    }];
    static outlets: IOutletsMeta = [{
        type: "anything",
        description: "Result"
    }];
    static props: IPropsMeta = {
        forceInt: {
            type: "boolean",
            default: false,
            description: "Convert inputs and outputs to integers"
        }
    };
    prepareInput(input: any) {
        const forceInt = this.getProp("forceInt");
        if (input instanceof Array && input.length == 1) {
            const first = +extractFirst(input);
            return forceInt ? Math.floor(first) : first;
        }
        return forceInt ? Math.floor(+input) : +input;
    }
    execute: (a: any) => any;
    _ = { result: undefined as any };
    subscribe() {
        super.subscribe();
        this.on("preInit", () => {
            this.inlets = 1;
            this.outlets = 1;
        });
        this.on("inlet", ({ data, inlet }) => {
            if (inlet === 0) {
                if (!isBang(data)) {
                    try {
                        this._.result = this.prepareInput(this.execute(this.prepareInput(data)));
                    } catch (e) {
                        this.error(e);
                        return;
                    }
                }
                this.outlet(0, this._.result);
            }
        });
    }
}
