import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Hip extends JsDspProcessor {
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "audio signal",
            varLength: true
        },
        {
            isHot: true,
            type: "signal",
            description: "rolloff frequency",
            varLength: true
        }
    ]
    static outlets: IOutletsMeta = [
        {
            type: "signal",
            description: "output",
            varLength: true
        }
    ]
    static args: IArgsMeta = [
        {
            type: "number",
            optional: true,
            description: "rolloff frequency",
            default: 100.
        }
    ]
    static argsOffset: number = 1;
    static docs: string = "math/docs/hip.html";

    sample_rate_: number;
    hz_: number;
    last_: number;
    coef_: number;

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        let inputStream = inputs[0][0];
        let frequency = inputs[0][1];
        let outputStream = outputs[0][0];

        let last = 0.;
        let coef = 0.;
        let normal = 0.;
        let newval = 0.;

        for (let i = 0; i < inputStream.length; i++) {

            this.hz_ = frequency[i];

            if (this.hz_ < 0.) {
                this.hz_ = 0.;
            }

            this.coef_ = 1 - (this.hz_ * (2 * Math.PI)) / this.sample_rate_;

            if (this.coef_ < 0.) {
                this.coef_ = 0.;
            }
            else if (this.coef_ > 1.) {
                this.coef_ = 1.;
            }

            coef = this.coef_;
            last = this.last_;

            if (coef < 1) {

                normal = 0.5 * (1 + coef);
                newval = inputStream[i] + coef * last;

                outputStream[i] = normal * (newval - last);

                last = newval;
                this.last_ = last;
            }
            else {
                outputStream[i] = inputStream[i];
                this.last_ = 0.;
            }


        }

        return true;
    }

    init(sampleRate: number) {

        this.sample_rate_ = sampleRate;
        this.hz_ = 0.;
        this.coef_ = 0.;
        this.last_ = 0.;

    }
}
