import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Bp extends JsDspProcessor {
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "audio signal",
            varLength: true
        },
        {
            isHot: true,
            type: "signal",
            description: "center frequency",
            varLength: true
        },
        {
            isHot: true,
            type: "signal",
            description: "Q (controls bandwidth)",
            varLength: true
        }

    ]
    static outlets: IOutletsMeta = [
        {
            type: "signal",
            description: "output",
            varLength: true
        }
    ]
    static args: IArgsMeta = [
        {
            type: "number",
            optional: true,
            description: "initial center frequency",
            default: 1000
        },
        {
            type: "number",
            optional: true,
            description: "initial q",
            default: 1
        }
    ]
    static argsOffset: number = 1;
    static docs: string = "math/docs/bp.html";


    sample_rate_: number;
    x1_: number;
    x2_: number;
    coef1_: number;
    coef2_: number;
    gain_: number;
    q_: number;
    freq_: number;
    f_: number;

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        let inputStream = inputs[0][0];
        let centerFrequency = inputs[0][1];
        let q = inputs[0][2];
        let outputStream = outputs[0][0];

        for (let i = 0; i < inputStream.length; i++) {


            // do coefficients

            let r = 0.;
            let oneminusr = 0.;
            let omega = 0.;
            let qcos = 0.

            if (centerFrequency[i] < 0.001) {
                centerFrequency[i] = 10.0;
            }

            if (q[i] < 0.) {
                q[i] = 0.;
            }

            this.freq_ = centerFrequency[i];
            this.q_ = q[i];

            omega = centerFrequency[i] * (2.0 * Math.PI) / this.sample_rate_;

            if (q[i] < 0.001) {
                oneminusr = 1.0;
            }
            else {
                oneminusr = omega / q[i];
            }

            if (oneminusr > 1.0) {
                oneminusr = 1.0;
            }

            r = 1.0 - oneminusr;

            if ((omega >= -(0.5 * Math.PI)) && (omega <= 0.5 * Math.PI)) {
                let g = omega * omega;
                qcos = (((g * g * g * (-1.0 / 720.0) + g * g * (1.0 / 24.0)) - g * 0.5) + 1);
            }
            else {
                qcos = 0.;
            }


            this.coef1_ = 2.0 * qcos * r;
            this.coef2_ = -r * r;
            this.gain_ = 2 * oneminusr * (oneminusr + r * omega);

            // perform calculations for output

            let last = this.x1_;
            let prev = this.x2_;
            let coef1 = this.coef1_;
            let coef2 = this.coef2_;
            let gain = this.gain_;

            let output = inputStream[i] + coef1 * last + coef2 * prev;

            outputStream[i] = gain * output;

            prev = last;
            last = output;

            this.x1_ = last;
            this.x2_ = prev;
        }

        return true;
    }

    init(sampleRate: number) {

        this.sample_rate_ = sampleRate;
        this.x1_ = 0.;
        this.x2_ = 0.;
        this.coef1_ = 0.;
        this.coef2_ = 0.;
        this.gain_ = 0.;
        this.q_ = 0.;
        this.freq_ = 0.;
        this.f_ = 0.;
    }
}
