import JsDspProcessor from "../../../../../common/web/jsDspProcessor";
import { IArgsMeta, IInletsMeta, IOutletsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Mul extends JsDspProcessor {
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "signal",
            description: "audio input connection 1",
            varLength: true
        },
        {
            isHot: true,
            type: "signal",
            description: "audio input connection 2",
            varLength: true
        }
    ]
    static outlets: IOutletsMeta = [
        {
            type: "signal",
            description: "audio output connection",
            varLength: true
        }
    ]
    static args: IArgsMeta = [
        {
            type: "number",
            optional: true,
            description: "initial argument",
            default: 0.
        }
    ]
    static argsOffset: number = 1;
    static docs: string = "math/docs/mul~.html";


    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {

        let in1 = inputs[0][0];
        let in2 = inputs[0][1];
        let outputStream = outputs[0][0];

        for (let i = 0; i < in1.length; i++) {
            outputStream[i] = in1[i] * in2[i];
        }

        return true;
    }
}
